g: 1rem;;\n    box-shadow: 4px 0px 5px rgba(0,0,0,0.2);\n    display: flex;\n    flex-direction: column;\n}\n\n.projects-container {\n    display: flex;\n    flex-direction: column;\n    gap: 0.5rem;\n    margin: 0 0.5rem;\n}\n\n#project-list {\n    display: flex;\n    flex-direction: column;\n    gap: 0.1rem;\n}\n\n.project-btn, .display-filter-btn {\n    text-align: left;\n    padding: 0.7rem 1rem;\n}\n\n#add-project-btn, #add-template-projects-btn {\n    font-size: 1.5rem;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: color-mix(in srgb, var(--button-color) 70%, white);\n    margin-bottom: 0.1rem;\n}\n\n#add-template-projects-btn {\n    font-size: 1rem;\n    gap: 0.5rem;\n}\n\n.display-filter-btn {\n    font-size: 1rem;\n    display: flex;\n    align-items: center;\n    gap: 0.5rem;\n    margin-bottom: 0.1rem;\n}\n\n#add-project-btn > .iconify, #add-template-projects-btn > .iconify {\n    font-size: 1.5rem;\n}\n\n#add-project-btn:hover, #add-template-projects-btn:hover {\n    background-color: color-mix(in srgb, var(--button-color) 60%, white);\n}\n\nbutton.active-tab {\n    border-left: 6px solid color-mix(in srgb, var(--button-color) 60%, white);\n}\n\n.active-tab > button#active-project {\n    border-left: 6px solid color-mix(in srgb, var(--button-color) 60%, white);\n}\n\n#add-project-form {\n    display: flex;\n    flex-direction: column;\n}\n\ninput, textarea, button {\n    background-color: var(--todo-item-color);\n    margin-bottom: 0.5rem;\n    padding: 0.5rem;\n    border: none;\n    border-radius: 5px;\n    font-family: \"Roboto\", Arial, sans-serif;\n    font-size: 1rem;\n    box-shadow: 0px 2px 5px rgba(0,0,0,0.2);\n}\n\ninput:focus, textarea:focus {\n    outline: none;\n}\n\ninput:disabled, textarea:disabled {\n    background-color: color-mix(in srgb, var(--todo-item-color) 80%, black);\n    cursor: not-allowed;\n}\n\n.todo-item-date-container{\n    display: grid;\n    grid-template-columns: auto 1fr;\n    gap: 1rem;\n}\n\nbutton, .project {\n    cursor: pointer;\n    background-color: var(--button-color);\n    font-weight: bold;\n    color: white;\n    transition: background-color 0.3s ease;\n}\n\nbutton:hover, .project:hover {\n    background-color: color-mix(in srgb, var(--button-color) 85%, white);\n}\n\n.form-buttons-project, .form-buttons-todo-list, .form-buttons-todo-item {\n    margin-top: 0.5rem;\n    display: flex;\n    gap: 0.5rem;\n}\n\n.form-buttons-project > button, .form-buttons-todo-list > button, .form-buttons-todo-item > button {\n    width: 200px;\n}\n\ndiv#main {\n    padding: 1rem;\n}\n\n#current-project {\n    background-color: color-mix(in srgb, var(--primary-color) 70%, white);\n    padding: 1rem;\n    margin-bottom: 1rem;\n    border-radius: 5px;\n    box-shadow: 0px 2px 5px rgba(0,0,0,0.2);\n}\n\n#todo-lists {\n    column-count: auto;\n    column-width: clamp(100px, 20rem, 450px);\n}\n\n.add-todo-list-btn {   \n    width: 100%;\n    font-size: 4rem;\n    box-shadow: 0px 2px 5px rgba(0,0,0,0.2);\n    -webkit-column-break-inside: avoid;\n    break-inside: avoid;\n    page-break-inside: avoid;\n}\n\n#add-todo-list-div, .add-todo-item-div {\n    display: flex;\n    flex-direction: column;\n    -webkit-column-break-inside: avoid;\n    break-inside: avoid;\n    page-break-inside: avoid;\n}\n\n.project-todo-list, .add-todo-list-form-active, .filter-todo-list {\n    background-color: color-mix(in srgb, var(--primary-color) 70%, white);\n    padding: 1rem;\n    margin-bottom: 1rem;\n    border-radius: 5px;\n    box-shadow: 0px 2px 5px rgba(0,0,0,0.2);\n    -webkit-column-break-inside: avoid;\n    break-inside: avoid;\n    page-break-inside: avoid;\n    display: flex;\n    flex-direction: column;\n    gap: 0.5rem;\n}\n\n.add-todo-list-form-active {\n    gap: 0;\n}\n\n.filter-todo-list {\n    margin: 0 auto 1rem auto;\n}\n\n.project-todo-list-top-content, .project-top-content, .filter-todo-list-top-content {\n    display: grid;\n    grid-templa